import { createApp } from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import "@/assets/styles/index.css"; // 引入全局样式
// 引入 bootstrap
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap/dist/js/bootstrap.min.js";
// 引入 view
import "view-ui-plus/dist/styles/viewuiplus.css";
import ViewUIPlus from "view-ui-plus";
// 引入 element
import "element-plus/dist/index.css";
import Element from "element-plus";
const app = createApp(App);
// 引入 v-md-editor 用来显示代码高亮 编辑
import VueMarkdownEditor from "@kangc/v-md-editor";
import "@kangc/v-md-editor/lib/style/base-editor.css";
import vuepressTheme from "@kangc/v-md-editor/lib/theme/vuepress.js";
import "@kangc/v-md-editor/lib/theme/style/vuepress.css";
import Prism from "prismjs";
VueMarkdownEditor.use(vuepressTheme, {
  Prism,
});
// 引入 v-md-editor 用来显示代码高亮 预览
import VMdPreview from "@kangc/v-md-editor/lib/preview";
import "@kangc/v-md-editor/lib/style/preview.css";
import githubTheme from "@kangc/v-md-editor/lib/theme/github.js";
import "@kangc/v-md-editor/lib/theme/style/github.css";
import hljs from "highlight.js";
VMdPreview.use(githubTheme, {
  Hljs: hljs,
});
// 引入屏幕适配插件
import "./utils/flexible";
// 引入全部图标 element 图标
import * as ElementPlusIconsVue from "@element-plus/icons-vue";
for (const [key, component] of Object.entries(ElementPlusIconsVue)) {
  app.component(key, component);
}
// 挂载实例
app.use(store);
app.use(ViewUIPlus); // 挂载 iview
app.use(Element); // 挂载 element
app.use(VueMarkdownEditor); // 挂载 v-md-editor // 编辑
app.use(VMdPreview); // 挂载 v-md-editor // 预览
app.use(router);
app.mount("#app");
