(function flexible(window, document) {
  // 获取html的根元素
  var docEl = document.documentElement; // 获取html的根元素
  var dpr = window.devicePixelRatio || 1; //dpr  物理像素比

  // adjust body font size  设置我们body 的字体大小
  function setBodyFontSize() {
    //函数
    if (document.body) {
      //如果有body  就执行下边的
      document.body.style.fontSize = 16 * dpr + "px"; //设置body字体大小
    } else {
      // 如果页面中没有body 这个元素  则等着 我们页面主要的DOM元素加载完毕 再去设置 body  的字体大小  DOMContentLoaded
      document.addEventListener("DOMContentLoaded", setBodyFontSize);
    }
  }
  setBodyFontSize(); //调用函数

  // set 1rem = viewWidth / 10   设置我们html  元素的字体大小
  function setRemUnit() {
    var rem = docEl.clientWidth / 24; //把屏幕划分24等份
    docEl.style.fontSize = rem + "px"; //html 的字体大小就等于 rem
  }

  setRemUnit(); //调用函数

  // reset rem unit on page resize  //当页面尺寸发生边话的时候  要重新设置rem  的大小
  window.addEventListener("resize", () => {
    // 设置最小宽
    if (window.innerWidth > 1200) setRemUnit();
  }); //resize是事件   当页面发生变化的时候 resize  去调用  setRemunti
  window.addEventListener("pageshow", function (e) {
    //pageshow事件  是我们重新加载页面的时候触发     无论事件是否来自缓存
    if (e.persisted) {
      // persisted 返回的是true  就是说如果这个页面是从缓存取过来的页面  也需要从新计算ren
      setRemUnit(); //调用  函数  setRemUnit
    }
  });

  // detect 0.5px supports  //有些移动端浏览器 不支持0.5像素 的写法   这个就是让那些不支持的支持  了解到就行
  if (dpr >= 2) {
    var fakeBody = document.createElement("body");
    var testElement = document.createElement("div");
    testElement.style.border = ".5px solid transparent";
    fakeBody.appendChild(testElement);
    docEl.appendChild(fakeBody);
    if (testElement.offsetHeight === 1) {
      docEl.classList.add("hairlines");
    }
    docEl.removeChild(fakeBody);
  }
})(window, document);
