import { createRouter, createWebHashHistory } from "vue-router";
const routes = [
  {
    path: "/",
    component: () => import("@/views/Layout"),
    // 路由不显示是因为 children 是一个数组
    children: [
      {
        path: "/",
        component: () => import("@/views/chatGPT/homeChat"),
      },
      {
        path: "/ChatGPT",
        name: "ChatGPT",
        component: () => import("@/views/chatGPT/chat"),
      },
      {
        path: "/ImageGeneration",
        name: "ImageGeneration",
        component: () => import("@/views/ImageGeneration/ImageGeneration"),
      },
    ],
  },
  // 高亮显示代码路由
  {
    path: "/MarkdownPracticeEditing",
    component: () => import("@/components/MarkdownPracticeEditing"),
  },
  {
    path: "/MarkdownPracticeDisplay",
    component: () => import("@/components/MarkdownPracticeDisplay"),
  },
];

const router = createRouter({
  history: createWebHashHistory("mobile"), // 页面找到了为空问题
  // history: createWebHistory(),
  routes,
});

export default router;
