import { createStore } from "vuex";

export default createStore({
  state: {
    InputSearch: null, // 保存首页输入框里边的内容
    stopTextStream: null, // 停止文本流
    temperature: "", // 要使用的采样温度，介于0和2之间。较高的值(如0.8)将使输出更具随机性，而较低的值(如0.2)将使输出更具针对性和确定性。
    presence_penalty: "", // 介于-2.0和2.0之间的数字。正值根据新词到目前为止是否出现在文本中来惩罚它们，从而增加了模型讨论新主题的可能性。
    frequency_penalty: "", // 介于-2.0和2.0之间的数字。到目前为止，正值根据新标记在文本中的现有频率来惩罚它们，从而降低了模型逐字重复相同行的可能性。
    messagesNumber: null, // 聊天的记忆
    PhonetisStyleStatus: null, // 录音状态loading
  },
  getters: {},
  mutations: {
    // 修改输入框里边的内容
    setInputSearch(state, value) {
      state.InputSearch = value;
    },
    // 停止文本流
    stopTextStream(state, value) {
      state.stopTextStream = value;
    },
    // 要使用的采样温度，介于0和2之间。较高的值(如0.8)将使输出更具随机性，而较低的值(如0.2)将使输出更具针对性和确定性。
    temperature(state, value) {
      state.temperature = value;
    },
    // 介于-2.0和2.0之间的数字。正值根据新词到目前为止是否出现在文本中来惩罚它们，从而增加了模型讨论新主题的可能性。
    presence_penalty(state, value) {
      state.presence_penalty = value;
    },
    // 介于-2.0和2.0之间的数字。到目前为止，正值根据新标记在文本中的现有频率来惩罚它们，从而降低了模型逐字重复相同行的可能性。
    frequency_penalty(state, value) {
      state.frequency_penalty = value;
    },
    messagesNumber(state, value) {
      state.messagesNumber = value;
    },
    // 录音状态loading
    PhonetisStyleStatus(state, value) {
      state.PhonetisStyleStatus = value;
    },
  },
  actions: {},
  modules: {},
});
